.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 14px;
  width: max-content;
  max-width: 100%;
  outline: 1px solid red;
  padding: 0;
  list-style: none;

  li {
    display: inline-flex;
  }
}

.social__link {
  display: inline-flex;
  // font-size: 0;
  outline: 1px solid red;
  width: 27px;
  height: 27px;
}
